import React, { useEffect } from "react";
import PaymentAccountApiClient from "api/admin/PaymentAccountApiClient";
import Container from "@material-ui/core/Container";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogActions from "@material-ui/core/DialogActions";
import DialogTitle from "@material-ui/core/DialogTitle";
import SettingsIcon from "@material-ui/icons/Settings";
import Table from "@material-ui/core/Table";
import TextField from "@material-ui/core/TextField";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { PaymentAccount } from "models/PaymentAccount";

import * as styles from "./styles.module.scss";

const paymentAccountApiClient = new PaymentAccountApiClient();

const AdminHeader = () => {
  const [open, setOpen] = React.useState(false);
  const [openModalAdd, setOpenModalAdd] = React.useState(false);
  const [paymentAccounts, setPaymentAccounts] = React.useState<Array<PaymentAccount>>([]);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClickOpenModalAdd = () => {
    setOpenModalAdd(true);
    setOpen(false);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleCloseModalAdd = () => {
    setOpenModalAdd(false);
    setOpen(true);
  };

  useEffect(() => {
    paymentAccountApiClient
      .getAllPaymentAccout()
      .then((res: any) => setPaymentAccounts(res));
  }, [paymentAccounts]);

  return (
    <Container className={styles.container} fixed>
      <Typography>
        ABC CASINO
      </Typography>
      <Box className={styles.boxContainer}>
        <SettingsIcon onClick={handleClickOpen} className={styles.icon} />
        <Typography>
          LOGOUT
        </Typography>
      </Box>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        className={styles.dialogContainer}
      >
        <DialogTitle id="alert-dialog-title">ALIPAY</DialogTitle>
        <DialogContent className={styles.dialog}>
          <DialogContentText id="alert-dialog-description">
            <TableContainer component={Paper}>
              <Table aria-label="simple table">
                <TableHead>
                  <TableRow>
                    <TableCell>Label</TableCell>
                    <TableCell align="right">QR</TableCell>
                    <TableCell align="right">Daily Limit</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {paymentAccounts !== [] && paymentAccounts.map(paymentAccount => (
                    <TableRow key={paymentAccount.paymentMethodId}>
                      <TableCell component="th" scope="row">
                        {paymentAccount.name}
                      </TableCell>
                      <TableCell align="right">
                        <img className={styles.qrImage} alt="qrCode" src={paymentAccount.qrCodeUrl} />
                      </TableCell>
                      <TableCell align="right">{paymentAccount.dailyLimit}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button color="primary" onClick={handleClickOpenModalAdd}>
            ADD
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog
        open={openModalAdd}
        onClose={handleCloseModalAdd}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        className={styles.dialogContainer}
      >
        <DialogTitle id="alert-dialog-title">ALIPAY</DialogTitle>
        <DialogContent className={styles.dialog}>
          <DialogContentText id="alert-dialog-description">
            <form className={styles.formData}>
              <TextField id="outlined-basic" label="Label" variant="outlined" />
              <Button
                variant="contained"
                component="label"
              >
                Upload QR Image
                <input type="file" style={{ display: "none" }} />
              </Button>
              <TextField id="outlined-basic" label="Bank" variant="outlined" />
              <TextField id="outlined-basic" label="Name" variant="outlined" />
              <TextField id="outlined-basic" label="Account No" variant="outlined" />
              <TextField id="outlined-basic" label="Daily Limit" variant="outlined" />
            </form>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button color="primary" onClick={handleCloseModalAdd}>
            Close
          </Button>
          <Button color="primary">
            Add
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default AdminHeader;
