import ChinaExchangeApiClient from "api/common/ChinaExchangeApiClient";

export default class TransactionApiClient extends ChinaExchangeApiClient {
  baseUrl = "api";

  getAllTransactions = (status?: any) => {
    const path = status ? `/admin/transactions?statuses=${status}` : "/";
    return this.get(path);
  };

  createTransaction = (data: any) => {
    return this.post("/admin/transactions", { data });
  };

  completeTransaction = (action: string, data: string) => {
    return this.put(`/transactions/${data}/${action}`, { data });
  };
}
