import React, { useEffect } from "react";
import "date-fns";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import DateFnsUtils from "@date-io/date-fns";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogTitle from "@material-ui/core/DialogTitle";
import { KeyboardDatePicker, MuiPickersUtilsProvider, } from "@material-ui/pickers";
import { Transaction } from "models/Transaction";
import { toCurrency } from "Utils/numberFormat";
import { DEFAULT_DATE_TIME_FORMAT } from "Utils/setting";
import TransactionApiClient from "api/admin/TransactionApiClient";
import { useTranslation } from "react-i18next";

import moment from "moment";

import * as styles from "./styles.module.scss";

const transactionApiClient = new TransactionApiClient();

const AdminPageContainer: React.FC = () => {
  const { t } = useTranslation();
  const [selectedStartDate, setSelectedStartDate] = React.useState<Date>(new Date("2014-08-18T21:11:54"));
  const [selectedEndDate, setSelectedEndDate] = React.useState<Date>(new Date("2014-08-18T21:11:54"));
  const [selectedStatus, setStatus] = React.useState<any>(["submitted"]);
  const [transactions, setTransactions] = React.useState<Array<Transaction>>([]);
  const [idSelectTransaction, handleSelectTransaction] = React.useState<any>("");
  const [action, setAction] = React.useState("");
  const [open, setOpen] = React.useState(false);

  useEffect(() => {
    transactionApiClient
      .getAllTransactions(selectedStatus)
      .then(trans => setTransactions(trans.docs));
  }, [selectedStatus]);

  const handleStartDateChange = (date: Date) => {
    setSelectedStartDate(date);
  };

  const handleEndDateChange = (date: Date) => {
    setSelectedEndDate(date);
  };

  const getTransaction = () => {
    transactionApiClient
      .getAllTransactions(selectedStatus)
      .then(trans => setTransactions(trans.docs));
  };

  const handleStatusChange = (status: any) => {
    setStatus(status);
  };

  const handleClickOpen = (data: string, id:string) => {
    setOpen(true);
    setAction(data);
    handleSelectTransaction(id);
  };

  const handleTransaction = () => {
    transactionApiClient
      .completeTransaction(action, idSelectTransaction).then(() => {
        getTransaction();
      })
      .then(() => {
        handleClose();
      });
  };

  const handleClose = () => {
    setOpen(false);
  };


  return (
    <Box p={4}>
      <Grid container spacing={3}>
        <Grid item xs={4}>
          <Card className={styles.card}>
            <CardContent className={styles.cardContent}>
              <Box>
                <Typography variant="h5">
                  {t("total deposit")}
                </Typography>
                <Typography>
                  {toCurrency(20000000)}
                </Typography>
              </Box>
              <Box>
                <Typography>
                  Last charged
                </Typography>
                <Typography>
                  2020.05.31
                </Typography>
                <Typography>
                  12:34:33
                </Typography>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={4}>
          <Card className={styles.card}>
            <CardContent className={styles.cardContent}>
              <Box>
                <Typography variant="h5">
                  Total Paid
                </Typography>
                <Typography>
                  {toCurrency(11000000)}
                </Typography>
              </Box>
              <Box>
                <Typography>
                  Last Paid
                </Typography>
                <Typography>
                  2020.05.24
                </Typography>
                <Typography>
                  23:11:33
                </Typography>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={4}>
          <Card className={styles.card}>
            <CardContent className={styles.cardContent}>
              <Box>
                <Typography variant="h5">
                  Blance
                </Typography>
                <Typography>
                  {toCurrency(9000000)}
                </Typography>
              </Box>
              <Box className={styles.btnContainer}>
                <Button variant="outlined" color="primary">
                  Withdraw
                </Button>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12}>
          <Box className={styles.tableAction}>
            <Box className={styles.btnGroup}>
              <ButtonGroup variant="text" color="primary" aria-label="text primary button group">
                <Button onClick={() => handleStatusChange(["submitted"])}>Pending</Button>
                <Button onClick={() => handleStatusChange(["approved"])}>Approved</Button>
                <Button onClick={() => handleStatusChange(["rejected"])}>Rejected</Button>
              </ButtonGroup>
            </Box>
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <Grid container className={styles.dateContainer}>
                <KeyboardDatePicker
                  className={styles.startDate}
                  margin="normal"
                  id="date-picker-dialog"
                  label="From"
                  format="MM/dd/yyyy"
                  value={selectedStartDate}
                  onChange={(event: any) => handleStartDateChange(event)}
                  KeyboardButtonProps={{
                    "aria-label": "change date",
                  }}
                />
                <KeyboardDatePicker
                  margin="normal"
                  id="date-picker-dialog"
                  label="To"
                  format="MM/dd/yyyy"
                  value={selectedEndDate}
                  onChange={(event: any) => handleEndDateChange(event)}
                  KeyboardButtonProps={{
                    "aria-label": "change date",
                  }}
                />
              </Grid>
            </MuiPickersUtilsProvider>
          </Box>
          <TableContainer component={Paper}>
            <Table aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell align="left">Date</TableCell>
                  <TableCell align="left">Type</TableCell>
                  <TableCell align="left">Agency</TableCell>
                  <TableCell align="left">Request Amount</TableCell>
                  <TableCell align="left">Status</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {transactions !== [] && transactions.map((transaction) => (
                  <TableRow key={transaction._id}>
                    <TableCell align="left">{moment(transaction.updatedAt).format(DEFAULT_DATE_TIME_FORMAT)}</TableCell>
                    <TableCell align="left">{transaction.paymentMethod.name}</TableCell>
                    <TableCell align="left">{transaction.consumer && transaction.consumer.fullName}</TableCell>
                    <TableCell align="left">{toCurrency(transaction.amount)}</TableCell>
                    <TableCell align="left">
                      {transaction.status === "submitted" &&
                        <>
                          <Button variant="contained" onClick={() => handleClickOpen("approve", transaction._id)} className={styles.btn} color="primary">
                            Paid
                          </Button>
                          <Button className={styles.btn} onClick={() => handleClickOpen("reject", transaction._id)} variant="contained" color="secondary">
                            Reject
                          </Button>
                        </>}
                      {transaction.status !== "submitted" && <p>Complete</p>}
                      <Dialog
                        open={open}
                        onClose={handleClose}
                        aria-labelledby="alert-dialog-title"
                        aria-describedby="alert-dialog-description"
                      >
                        <DialogTitle id="alert-dialog-title">Are you sure?</DialogTitle>
                        <DialogActions>
                          <Button onClick={handleTransaction} color="primary">
                            Yes
                          </Button>
                          <Button onClick={handleClose} color="primary" autoFocus>
                            No
                          </Button>
                        </DialogActions>
                      </Dialog>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
      </Grid>
    </Box>
  );
};


export default AdminPageContainer;
